import getService from "../../services";

export function loadMatches() {
  debugger;
  return dispatch => {
    return getService(`http://localhost:5000/api/matches/75`, "GET")
      .then(response => {
        dispatch({ type: "LOAD_MATCHES", data: response.data });
      })
      .catch(e => console.log(e));
  };
}
export function incMatches(page) {
  debugger;
  return dispatch => {
    return getService(`http://localhost:5000/api/matches/${page}`, "GET")
      .then(response => {
        dispatch({ type: "INC_MATCHES", data: response.data });
      })
      .catch(e => console.log(e));
  };
}
